@import "./../../styles/variables";

.input {
  appearance: none;
  background-color: #fff;
  margin: 0;
  font: inherit;
  color: currentColor;
  width: 1.5em;
  height: 1.5em;
  border: 0.15em solid $grey;
  border-radius: 50%;
  transform: translateY(-0.075em);
  display: grid;
  place-content: center;

  &:checked {
    border-color: $secondary-light;

    &.success {
      border-color: $success-light;

      &::before {
        box-shadow: inset 1em 1em $success;
      }
    }

    &.warn {
      border-color: $warn-light;

      &::before {
        box-shadow: inset 1em 1em $warn;
      }
    }

    &::before {
      transform: scale(1);
    }
  }

  &::before {
    content: "";
    width: 0.85em;
    height: 0.85em;
    border-radius: 50%;
    transform: scale(0);
    transition: 120ms transform ease-in-out;
    box-shadow: inset 1em 1em $secondary;
  }
}
