.svgIndicator {
  width: 50px;
  height: 50px;
  transform: rotate(-90deg);
}

.svgIndicatorTrack,
.svgIndicatorIndication {
  cx: 25px;
  cy: 25px;
  r: 20px;
  fill: transparent;
  stroke-width: 5px;
}

.svgIndicatorTrack {
  stroke: #eaeaea;
}

.svgIndicatorTrackWhite {
  stroke: #ffffff;
}

.svgIndicatorIndication {
  stroke: #07c;
  stroke-dasharray: 125.6px;
  transition: stroke-dashoffset 1s ease;
}

.orangeThick {
  stroke: #ff8c38;
}

.orangeLight {
  stroke: #fcb732;
}

.greenLight {
  stroke: #45de2c;
}
